<template>
  <div class="main-div">
    <div class="main-bg" @click="bgPressed">
      <DialogueTheme id="theme" :msgs="msgs" :speaker="speaker" :finishConversation="finishConversation" v-if="showDialogue" />
    </div>
  </div>
</template>

<script>
import DialogueTheme from '@/components/DialogueTheme'

export default {
  name: 'MapEntrance',
  components: {
    DialogueTheme
  },
  data () {
    return {
      speaker: '',
      msgs: ['這個區域有異常聚集的怪力，受到影響的人們變得像魚群一樣，流向四面八方。'],
      showDialogue: false
    }
  },
  mounted () {
    this.$store.commit('changeHeader', false)
  },
  methods: {
    bgPressed () {
      this.showDialogue = true
    },
    finishConversation (msgs) {
      this.$router.push({name: 'CommuteEscalatorShowUp'})
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "~@/assets/scss/common.scss";
  .main-div {
    @extend fullScreenImage;
    background-image: url('~@/assets/images/commute/main-bg.png');
    .main-bg {
      @extend fullScreenImage;
      background-image: url('~@/assets/images/commute/black-map.png');
      position: relative;
      #theme {
        position: absolute;
        bottom: 24%;
      }
    }
  }
</style>



